.Sidebar{
    display: flex;
    flex-direction: column;
    position: relative;
    padding-top: 2rem;
    transition: all 300ms ease;
}

.logo{
    display: flex;
    height: 5rem;
    font-weight: bold;
    font-size: 22px;
    gap: 1rem;
    justify-content: center;
    align-items: center;

}

.logo>img{
    width: 3rem;
    height: 3rem;
}

.logo>span>span{
    color: var(--pink);
}

/* menu */

.menu{
    margin-top: 4rem;
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

.menuItem{
    display: flex;
    align-items: center;
    gap: 1rem;
    height: 2.5rem;
    margin-left: 2rem;
    position: relative;
    transition: all 300ms ease;
    font-size:14px;
    border-radius: 0.7rem;
}

.menuItem:hover{
    cursor: pointer;
}

.active{
    background:var(--activeItem);
    margin-left: 0;
}
.active::before{
    content: '';
    width: 8px;
    height: 100%;
    background: var(--pink);
    margin-right: calc(1rem -8px);
}

.menu .menuItem:last-child{
    position: absolute;
    bottom: 3.3rem;
    width: 10%;
   
}

.menuItem:hover{
    background:var(--activeItem);
    margin-left: 0;
   
}

.menuItem::before{
    content: '';
    width: 8px;
    height: 100%;
    background: var(--pink);
    margin-right: calc(1rem -8px);
}

/*responsive using @media querires */

@media screen and (max-width:1200px){
    .menuItem>span{
       display: none;
    }
    .logo{
        display: none;
    }
}
.MuiTableRow-root > * {
    padding: 10px;
  }
  
  .MuiTableRow-root.MuiTableRow-head > * {
    font-weight: bold !important;
  }
  
  .Table td,
  th {
    border: none !important;
  }
  
  .Table:first-child {
    border-radius: 0.7rem !important;
  }
  .status {
    padding: 8px;
    border-radius: 9px;
  }
  
  .Details {
    color: #00b5ff !important;
  }

  .bars{
    display: none;
  }
  
  @media screen and (max-width: 1200px) {
    .Table {
      width: 170%;
      margin-top: 2rem;
    }
  }
  
  
  @media screen and (max-width: 768px) {
   .Sidebar{
    position:fixed;
    z-index: 9;
    background: #ffe0e0;
    width: 55%;
    padding-right: 1rem;
    height: 100%;
   }
   .menuItem>span{
    display: block;
   }
   .logo{
    display: flex;
   }
   .menu .menuItem:last-child{
    position: relative;
    margin-top: 4rem;
   }
   .bars{
    display: flex;
    position:fixed;
    top: 2rem;
    z-index: 9;
    background: #ffe0e0;
    padding: 10px;
    border-radius: 10px;
   }
  }
  